import uniBadge from "@/components/uni-badge/uni-badge.vue"


export default{
	components: {uniBadge},
	data(){
		return{
			theMid:'',
			moveLineOffset: 12.5,
			currentIndex: 1,
			showNewsData: [
				{
					title:'系统通知',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				},
				{
					title:'会员通知',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				},
				{
					title:'订单通知',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				},
				{
					title:'我的评价',
					content:'618最后冲刺，看直播抽免单、赢万千618最后冲刺，看直播抽免单、赢万千',
					time:1593571037
				}
			],
		}
	},
	
	onLoad(eee) {
		
		var _this = this;
		uni.getStorage({
			key: 'mid',
			success: function(res) {
				console.log('getStoragegemid' + res.data);
				_this.theMid = res.data;
				_this.getServicList();
			}
		});
		this.getServicList();
	},
	methods:{
		
		
		async getServicList(){
			
			var serverUrl="Home/Message/getList";
			var data={
				mid:this.theMid,
				type:this.currentIndex,
				limit:10,
			    page:this.page
			}
			var res = await this.requestFromServer({
				url:serverUrl,
				data:data
			})
			if(this.page==1){
				this.showNewsData=[];
			}
			uni.stopPullDownRefresh();
			if(res.data){
				this.showNewsData=this.showNewsData.concat(res.data);
				if(res.data.length<10){
					this.loadingText='没有更多数据了';
				}else{
					this.loadingText='加载更多数据...';
				}
			}else{
				this.loadingText='没有更多数据了';
			}
			
		},
		
		clickMe(index){
			if(index<=2){
				uni.navigateTo({
					url:'./messageList?query='+index
				})	
			}else if(index==3){
				uni.navigateTo({
					url:'./commentList'
				})	
			}
		    
		},
		
		dateFormat:function(time) {
			return this.getDateSecond(time);
		},
	}
}
